/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html, body {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
  font-family: sans-serif;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-eight: 1;
}
ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

body {
  background: white;
}

.home {
  .logo-wrapper {
    display: flex;
    justify-content: center;
    margin-top: 100px;
    height: 100%;
    .logo {
      width: 500px;
    }
  }
}

ul.social-icons {
  position: absolute;
  bottom: 25px;
  text-align: center;
  width: 100%;
  padding: 0;
}

ul.social-icons li {
  display: inline-block;
  margin-right: 75px;
}

ul.social-icons li:last-child {
  margin: 0;
}

ul.social-icons li a {
  color: #424949;
}

ul.social-icons li a:hover {
  color: #3398CB;
}

$navheight: 50px;
nav {
  height: $navheight;
  display: flex;
  width: 100%;
  border-bottom: 1px solid #ebebeb;
  position: sticky;
  top: 0;
  background-color: white;
  overflow: hidden;
  align-items: center;
  ul {
    margin-top: 0;
    margin-bottom: 0;
    padding-left: 0;
    li.home-li {
      width: $navheight;
      transition: none;
      a {
        padding: 0;
      }
      .logo-nav {
        max-height: ($navheight - 8);
        position: absolute;
        top: 4px;
        left: 4px;
      }
    }
    li {
      display: inline-block;
      transition: background-color 0.3s;
      a {
        padding: 10px;
        color: #333;
        font-family: sans-serif;
        text-decoration: none;
        line-height: $navheight;
      }
    }
    li:hover {
      background-color: rgba(#3398CB, 90%);
      a {
        color: white;
      }
    }
  }
}

a {
  color: #3398CB;
}

.sponsorship {
  font-family: sans-serif;
  display: flex;
  justify-content: center;
  flex-direction: column;
  .logo-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 40px;
    padding-bottom: 40px;
    .logo {
      width: 120px;
      height: 120px;
    }
  }
  section {
    max-width: 900px;
    margin-left: auto;
    margin-right: auto;
    color: #333;
    p {
      text-align: justify;
    }
  }
  table {
    width: 100%;
  }
  table, th, td {
    border: 1px solid #333;
    border-collapse: collapse;
  }
  td {
    padding: 10px;
    width: 50%;
  }
  thead {
    font-weight: bold;
    font-size: 30px;
  }
  .chart-wrapper {
    display: flex;
    justify-content: center;
    .chart {
      width: 50%;
    }
  }
}

.about {
  section {
    max-width: 900px;
    margin-left: auto;
    margin-right: auto;
    color: #333;
    p {
      text-align: justify;
    }
  }
}

.twoImages {
  display: flex;
  img {
    flex: 1;
    width: 50%;
  }
}

h1 {
  font-size: 40px;
}

.text-centered {
  text-align: center;
}